@import url(https://fonts.googleapis.com/css2?family=Khula:wght@300&display=swap);
body {
  height: 100vh;
  margin: 0;
  background-image: linear-gradient(rgb(177, 209, 211), rgb(75, 70, 77));
}

#root {
  height: 100%;
  position: relative;
}
.app {
  margin: 0;
  position: absolute;
  top: 50%;
  Left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Khula', sans-serif;
  font-size: larger;
}

h1, p {
  margin: 0;
  text-align: center;
}
